import React, { Component } from 'react';
export default class TranLinks extends Component<Props> {
  render() {
    return (
<div id="mytranslators">
<style jsx="true">{`
#mytranslators{
  text-align: center;
  max-width: 500px;
  margin: 0 auto 1em;
}
span {
  margin: 0 .2em;
}
`}</style>
<span><a href="http://learnlayout.com">english</a></span>
<span><a href="http://es.learnlayout.com">espaol</a></span>
<span><a href="http://fr.learnlayout.com">franais</a></span>
<span><a href="http://de.learnlayout.com">deutsch</a></span>
<span><a href="http://nl.learnlayout.com">dutch</a></span>
<span><a href="http://it.learnlayout.com">italiano</a></span>
<span><a href="http://ru.learnlayout.com"></a></span>
<span><a href="http://fa.learnlayout.com"></a></span>
<span><a href="http://ar.learnlayout.com"></a></span>
<span><a href="http://zh.learnlayout.com"></a></span>
<span><a href="http://zh-tw.learnlayout.com"></a></span>
<span><a href="http://ko.learnlayout.com"></a></span>
<span><a href="http://ja.learnlayout.com"></a></span>
<span><a href="http://pt-br.learnlayout.com">portugus(brasil)</a></span>
<span><a href="http://pt-pt.learnlayout.com">portugus(portugus)</a></span>

</div>
    );
  }
}



